<template>
  <div>
    <Loader v-if="loading" />
    <div v-if="dataPosts.length > 0">
      <div v-for="(user, index) of dataPosts" :key="index">
        <v-lazy 
          v-model="isView" 
          :options="{threshold: .5}" 
          transition="fade-transition" 
          min-height="200">
          <v-card class="account__post" @mousemove.once="postView(user)">
            <div
              class="account__post__top d-flex flex-row justify-space-between align-center"
            >
              <div class="d-flex flex-row align-center">
                <v-img
                  v-if="isImg && nameData.additionally.img"
                  :src="getImage"
                  class="account__post__image"
                >
                  <!-- <div v-if="nameData.online" class="account__post__online"></div>
                  <div v-else class="account__post__not-online"></div> -->
                </v-img>
                <div v-else class="account__post__image">
                  <!-- <div v-if="nameData.online" class="account__post__online"></div>
                  <div v-else class="account__post__not-online"></div> -->
                </div>
                <!-- <div v-else class="account__post__image"></div> -->
                <h2>{{ nameData.firstname }} {{ nameData.lastname }}</h2>
                <v-chip
                  color="myPurple"
                  style="cursor: pointer; color: #fff !important;"
                  class="ma-2"
                  v-if="user.category"
                >
                  {{ user.category }}
                </v-chip>
                  <v-tooltip top>
                    <template v-slot:activator="{ on }">
                      <v-chip v-on="on" class="account__post__level" color="myPurple" >
                        {{ user.authorLevel }} level
                      </v-chip>
                    </template>
                  <span>{{ rank }}</span>
                  </v-tooltip>
              </div>
              <h3 class="account__post__date">{{ user.date }}</h3>
              <v-menu offset-y>
                <template v-slot:activator="{ on }">
                  <v-icon color="purple accent-4" dark v-on="on">menu</v-icon>
                </template>
                <v-list>
                  <!-- <v-list-item v-if="isUser" class="account__post__edit">
                    <v-icon>create</v-icon>
                    <v-list-item-title style="margin-left: 10px;">
                      Редактировать
                    </v-list-item-title>
                  </v-list-item> -->
                  <v-list-item 
                    v-if="isUser" 
                    class="account__post__edit" 
                    @click="postRemove(user)"
                  >
                    <v-icon>cancel</v-icon>
                    <v-list-item-title style="margin-left: 10px;">Удалить</v-list-item-title>
                  </v-list-item>
                  <v-list-item 
                    v-if="myUser.bookmarks.includes(user.id) == false && !isBookmark" 
                    class="account__post__edit" @click="postSave(user)"
                  >
                    <v-icon>bookmark_border</v-icon>
                    <v-list-item-title style="margin-left: 10px;">
                      Сохранить в закладки
                    </v-list-item-title>
                  </v-list-item>
                  <v-list-item v-else class="account__post__edit" @click="postDelete(user)">
                    <v-icon>bookmark</v-icon>
                    <v-list-item-title style="margin-left: 10px;">
                      Удалить из закладок
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </div>
            <div class="account__post__text" v-html="user.text"></div>
            <div class="account__post__images d-flex justify-center" v-if="user.images">
              <div v-for="(img, index) of user.images" :key="index">
                <v-img
                  @mousemove="isImage = img.source"
                  @mouseout="isImage = false"
                  style="margin-bottom: 20px; padding: 10px" 
                  max-height="400" 
                  max-width="630" 
                  :src="'/' + img"
                >
                  <v-btn 
                    style="border: 2px solid #fff" 
                    dark 
                    @mousemove="isImage = img.source" 
                    v-if="isImage == img.source"
                    @click="imageAdd(img)"
                  >
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-img>
              </div>
            </div>
            <div class="account__post__bottom d-flex flex-row">
              <div 
                class="account__post__stats account__post__likes d-flex flex-row" 
                @click="addLike(user)"
              >
                <v-icon>favorite</v-icon>
                <h3 v-if="user.countLikes">{{ user.countLikes }}</h3>
                <h3 v-else>0</h3>
              </div>
              <div class="account__post__stats account__post__comments d-flex flex-row">
                <v-icon>comment</v-icon>
                <h3 v-if="user.countComments">{{ user.countComments }}</h3>
                <h3>0</h3>
              </div>
              <div
                class="account__post__stats account__post__views d-flex flex-row"
                :title="user.views + ' Просмотров'">
                <v-icon>visibility</v-icon>
                <h3>{{ user.views }}</h3>
              </div>
            </div>
            <AccountComments :postId="user" :isUser="isUser" />
          </v-card>
        </v-lazy>
      </div>
    </div>
    <div 
      class="account__post__nopost d-flex flex-row align-center" 
      v-else-if="!loading && !dataPosts.length"
    >
      <h2 v-if="isUser">Вы не опубликовали ни одного поста</h2>
      <h2 v-else>Данный пользователь не опубликовал ни одного поста</h2>
      <v-img src="../assets/nopost.png" width="300" height="300"></v-img>
    </div>
    <v-snackbar v-model="isDelete">
      <p>Пост удален. Для просмотра обновите страницу.</p>
      <v-btn color="#7f5af0" text @click="isDelete = false">Закрыть</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import AccountComments from "../components/accounts/AccountComments.vue";
import Loader from "./Loader.vue";
import { RankList } from "../models/models.js";
import axios from "axios";
import "../scss/posts.scss";

export default {
  name: "Post",
  components: {
    AccountComments,
    Loader
  },
  data() {
    return {
      myUser: "",
      dataPosts: "",
      isDelete: false,
      dataLike: "",
      loading: true,
      isView: false,
      rank: "",
      nameData: "",
      isBookmark: false,
      isImg: false,
      isImage: false
    };
  },
  props: {
    dataDB: Object,
    isUser: Boolean,
  },
  async created() {
    this.myUser = JSON.parse(localStorage.getItem("user"))
    let response = await axios.get("/api/posts/get_posts/" + this.$route.params.id,
      { headers: { Authorization: localStorage.getItem("jwt") }});

    this.dataPosts = response.data;
    let author = await axios.get("/api/users/find_user/" + this.$route.params.id,
      { headers: { Authorization: localStorage.getItem("jwt") }})
      
    this.nameData = author.data
    this.loading = false
    this.dataPosts = this.dataPosts.reverse()
    this.isImg = true
  },
  computed: {
    getImage() {
      return '/' + this.nameData.additionally.img
    }
  },
  sockets: {
    getPostsNews: function(data) {
      console.log(data)
      this.loading = false
      this.dataPosts = data
      this.isImg = true
    }
  },
  methods: {
    async postView(post) {
      try {
        let rank = new RankList()
        rank.rankLevel = post.authorLevel
        this.rank = rank.rankLevel
        await axios.put("/api/posts/post_view", { id: post._id, viewsId: this.dataDB.id});
      } catch (error) {
        console.log(error.message);
      }
    },
    async postRemove(post) {
      try {
        await axios.delete("/api/posts/remove_post/" + post.id );
        axios.delete("/api/comments/remove_comments/" + post._id);
        axios.delete("/api/likes/remove_likes/" + post._id );
        this.isDelete = true;
      } catch (error) {
        console.log(error.message);
      }
    },
    async addLike(post) {
      try {
        let response = await axios.put("/api/likes/add_like/" + this.dataDB.id, post)
        this.dataLike = response.data
        let count = await axios.put("/api/posts/count_like", 
          { count: this.dataLike, post: post })
        post.countLikes += parseInt(count.data)
      } catch (error) {
        console.log(error.message)
      }
    },
    async postSave(post) {
      try {
        let res = await axios.put("/api/users/post_save", 
          { postId: post.id, myId: this.myUser.id })
        localStorage.setItem("user", JSON.stringify(res.data))
        this.isBookmark = true
      } catch (error) {
        console.log(error.message)
      }
    },
    async postDelete(post) {
      try {
        let res = await axios.delete("/api/users/post_delete/" 
          + post.id + '/' + this.myUser.id)
        localStorage.setItem("user", JSON.stringify(res.data))
      } catch (error) {
        console.log(error.message)
      }
    },
    async imageAdd(img) {
      let res = await axios.put("/api/users/add_image", { img: img, user: this.myUser.id })
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  },
};
</script>