<template>
  <div>
    <div>
      <v-dialog 
        v-model="isFriendModal" 
        fullscreen
        transition="dialog-bottom-transition"
      >
      <v-card>
        <v-toolbar dark color="myPurple">
          <v-btn icon dark @click="$emit('input', false)">
            <v-icon size="32">mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title class="dialog__title">Список друзей</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <div class="d-flex flex-row flex-wrap">
        <div v-for="(friend, index) of dataFriends" :key="index">
          <div class="dialog__friend d-flex flex-column align-center">
            <div class="dialog__friend__image"></div>
            <router-link 
              :to="{name: 'Reload', params: {id: friend.id}}" 
              class="dialog__friend__title"
            >
              {{ friend.firstname }} {{ friend.lastname }}
            </router-link>
          </div>
        </div>
        </div>
      </v-card>
    </v-dialog>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AccountFriendModal",
  data() {
    return {
      dataFriends: []
    }
  },
  props: {
    isFriendModal: Boolean,
    friendArr: Array
  },
  async created() {
    let res = await axios.get("/api/users/find_friends/" + this.friendArr)
    this.dataFriends = res.data
  }
}
</script>

<style lang="scss" scoped>
.dialog {
  &__friend {
    margin-top: 30px;
    margin-left: 50px;
    &__title {
      color: #7f5af0;
      font-size: 18px;
      font-weight: 500;
      text-decoration: none;
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
    &__image {
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background-color: #2cb67d;
      cursor: pointer;
    }
  }
  &__title {
    font-size: 24px;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
  }
}
</style>