<template>
  <div>
    <NotAuth>
      <template v-slot:loading></template>
      <template v-slot:default>
        <v-card class="account__wall">
          <h2 v-if="isUser">Моя стена</h2>
          <h2 v-else>Стена</h2>
          <div class="account__wall__top d-flex flex-row">
            <v-img 
              v-if="user.additionally.img"
              :src="'/' + user.additionally.img" 
              class="account__wall__image"
            >
            </v-img>
            <div v-else class="account__wall__image"></div>
            <v-textarea 
              class="account__wall__input"
              solo 
              rows="1" 
              auto-grow
              color="myPurple" 
              v-model="text"
              label="Поделитесь вашим мнением по поводу данного пользователя">
            </v-textarea>
            <v-btn @click="commentSend" color="myPurple" class="account__wall__send">
              <v-icon>send</v-icon>
            </v-btn>
          </div>
          <div class="account__wall__comments" v-if="comments.length > 0">
            <div v-for="(comment, index) of comments" :key="index">
              <div class="account__wall__item d-flex flex-row align-center">
                <div>
                  <v-img 
                    v-if="comment.additionally.img"
                    :src="'/' + comment.additionally.img" 
                    class="account__wall__image"
                  >
                  </v-img>
                  <div v-else class="account__wall__image"></div>
                </div>
                <div class="account__wall__text">
                  <div class="d-flex flex-row align-center">
                    <router-link 
                      class="account__wall__link" 
                      :to="{ name: 'Reload', params: { id: comment.id }}"
                    >
                      {{ comment.firstname }} {{ comment.lastname }}
                    </router-link>
                    <v-card-subtitle>{{ comment.date }}</v-card-subtitle>
                    <v-icon 
                      style="cursor: pointer;"
                      v-if="isUser || comment.authorId == user.id" 
                      @click="commentDel(comment)"
                    >
                      delete
                    </v-icon>
                  </div>
                  <p>{{ comment.text }}</p>
                </div>
              </div>
            </div>
          </div>
          <div 
            class="account__wall__nocomments d-flex flex-row align-center" 
            v-else
          >
            <p>На вашей стене отсутствуют комментарии</p>
            <v-img src="../../assets/error.jpg" max-width="200" max-height="200"></v-img>
          </div>
        </v-card>
        <v-snackbar v-model="isPublic" :timeout="timeout">
          <p>Ваш комментарий опубликован</p>
          <v-btn color="myPurple" style="color: #fff" @click="isPublic = false">
            Закрыть
          </v-btn>
        </v-snackbar>
        <v-snackbar v-model="isDelete" :timeout="timeout">
          <p>Комментарий удален</p>
          <v-btn color="myPurple" style="color: #fff" @click="isDelete = false">
            Закрыть
          </v-btn>
        </v-snackbar>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import NotAuth from "../NotAuth.vue";
import axios from "axios";
import moment from "moment";
import "../../scss/accountWall.scss";
import "../../scss/mediaAccount.css";

export default {
  name: "AccountWall",
  components: { NotAuth },
  data() {
    return {
      commentList: [],
      comments: [],
      text: "",
      user: "",
      isDelete: false,
      isPublic: false,
      timeout: 2000
    }
  },
  props: {
    dataDB: Object,
    isUser: Boolean
  },
  async created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    let res = await axios.get("/api/comments/get_author_comments/" + this.dataDB.id)
    this.commentList = res.data
    for (let comment of this.commentList) {
      let res = await axios.get("/api/users/find_user/" + comment.authorId,
      { headers: { Authorization: localStorage.getItem("jwt") }})
      this.comments.push(Object.assign(res.data, comment))
    }
  },
  methods: {
    async commentSend() {
      await axios.put("/api/comments/add_author_comment", 
        { text: this.text, author: this.dataDB.id, id: Math.random(),
        toAuthor: this.$route.params.id, date: moment().format('lll') })
      this.text = ""
      this.isPublic = true
    },
    async commentDel(comment) {
      await axios.delete("/api/comments/del_comment/" + comment.commentId)
      this.isDelete = true
    }
  }
};
</script>
