<template>
  <div>
    <v-dialog persistent v-model="isModal" max-width="1170">
      <v-card>
        <div class="d-flex flex-row align-center justify-space-between">
          <v-card-title class="headline">Оформление профиля</v-card-title>
          <v-icon 
            @click="$emit('modalFalse', false)" 
            style="margin-right: 20px; cursor: pointer" 
            size="48"
          >
            close
          </v-icon>
        </div>
        <form>
        <div class="d-flex flex-column align-center">
          <h2 style="margin-bottom: 20px;">Вставьте изображение сюда</h2>
            <input @input="getImage" type="file" id="uploadd" class="hide" />
            <label for="uploadd">
              <div v-if="!filename">
                <v-icon style="margin-bottom: 5px;">save</v-icon>
                Загрузить изображение
              </div>
              <p v-else>Изображение - {{ filename }}</p>
              <v-img max-width="700" max-height="300" v-if="preImage" :src="preImage"></v-img>
            </label>
        </div>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="$emit('modalFalse', false)">
            Отмена
          </v-btn>
          <v-btn 
            color="myPurple" 
            outlined 
            v-if="fone.name"
            @click="foneSave"
          >
            Сохранить
          </v-btn>
          <v-btn disabled v-else>Сохранить</v-btn>
        </v-card-actions>
        </form>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AccountFoneModal",
  data() {
    return {
      user: "",
      filename: "",
      fone: "",
      preImage: null
    };
  },
  props: {
    isModal: Boolean,
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"))
  },
  methods: {
    getImage(event) {
      this.fone = event.target.files[0]
      this.filename = this.fone.name
      if (this.fone) {
        let reader = new FileReader()
        reader.onload = (e) => this.preImage = e.target.result
        reader.readAsDataURL(event.target.files[0])
      }
    },
    async foneSave() {
      let formData = new FormData()
      formData.set("fone", this.fone, this.fone.name)
      let res = await axios.put("/api/users/put_fone/" + this.user.id, formData,
        { headers: { "Content-Type": undefined }})
      await localStorage.setItem("user", JSON.stringify(res.data))
      this.$emit('modalFalse', false)
    }
  }
};
</script>

<style lang="scss" scoped>
.hide {
  position: absolute;
  overflow: hidden;
  z-index: -1;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
}
label {
  display: inline-block;
  cursor: pointer;
  background-color: #4CAF50;
  padding: 12px 30px;
  box-shadow: 0 3px 3px rgba(0,0,0,0.2);
  color: #fff;
  transition: all 0.3s;
  font-size: 18px;
  &:hover {
    box-shadow: 
    0 1px 1px rgba(0,0,0,0.11), 0 2px 2px rgba(0,0,0,0.11), 
    0 4px 4px rgba(0,0,0,0.11), 0 8px 8px rgba(0,0,0,0.11), 
    0 16px 16px rgba(0,0,0,0.11), 0 32px 32px rgba(0,0,0,0.11);
  }
}
</style>
