<template>
  <div>
    <v-card class="account__left">
      <v-card-title class="account__left__title">Подробные сведения</v-card-title>
      <v-card-subtitle style="margin-top: 20px;" class="account__left__info"
        >Семейный профиль: <span>Не указано</span></v-card-subtitle>
      <v-card-subtitle v-if="user.age" class="account__left__info"
        >Возраст: <span>{{ user.age }}</span></v-card-subtitle>
      <v-card-subtitle v-if="user.development != 'Не указано'" class="account__left__info"
        >Деятельность: <span>{{ user.development }}</span></v-card-subtitle>
      <v-card-subtitle v-if="user.interesting != 'Не указано'" class="account__left__info">Интересы:
        <span>{{ user.interesting }}</span></v-card-subtitle>
      <v-card-subtitle v-if="user.music != 'Не указано'" class="account__left__info"
        >Любимая музыка и исполнители:
        <span>{{ user.music }}</span></v-card-subtitle>
      <v-card-subtitle v-if="user.films != 'Не указано'" class="account__left__info"
        >Любимые фильмы, сериалы, телешоу:
        <span>{{ user.films }}</span></v-card-subtitle>
      <v-card-text v-if="user.about != 'Не указано'" class="account__left__info">О себе:
        <span style="margin-left: 10px;">{{ user.about }}</span>
      </v-card-text>
      <div>
        <div v-for="(category, index) of user.category" :key="index">
         <v-card-subtitle class="account__left__info">
          {{ category.title }}:
          <span>{{ category.text }}</span>
        </v-card-subtitle>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import { User } from "../../models/models.js";
import "../../scss/mediaAccount.css";

export default {
  name: "AccountLeft",
  data() {
    return {
      addition: ""
    }
  },
  props: {
    dataDB: Object
  },
  created() {
    this.user = new User(this.dataDB)
    this.user = this.user.findAddition
    
  }
};
</script>

<style lang="scss" scoped>
.account {
  &__left {
    margin-top: 50px;
    min-height: 120px;
    width: 400px;
    margin-bottom: 50px;
    &__title {
      font-size: 24px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    }
    &__info {
      margin-top: 10px;
      font-size: 18px;
      font-weight: bold;
      line-height: 20px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2) !important;
      span {
        font-weight: normal;
        font-size: 16px;
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
}
</style>
