<template>
  <div>
    <v-carousel v-if="images" class="account__slider">
      <v-carousel-item
        v-for="(image, index) in images"
        :key="index"
        reverse-transition="fade-transition"
        transition="fade-transition"
        max-width="1120"
      >
        <v-img :src="'../../../../server/' + image.source">
          <router-link 
            class="display-1 account__slider__title"
            :to="{name: 'Gallery', params: {id: $route.params.id}}"
          >
            Галерея
          </router-link>
        </v-img>
      </v-carousel-item>
  </v-carousel>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AccountSlider",
  data() {
    return {
      images: []
    }
  },
  async created() {
    let res = await axios.get("/api/users/get_gallery/" + this.$route.params.id)
    if (res.data.length > 0) this.images = res.data
    if (this.images.length > 6) this.images = this.images.slice(0, 6)
  }
};
</script>

<style lang="scss" scoped>
.account {
  &__slider {
    margin-top: 50px;
    &__title {
      color: #fff;
      text-align: center;
      text-decoration: none;
      font-weight: bold !important;
      cursor: pointer; 
      background: rgba(46, 46, 46, 0.5);
      max-width: 150px;
    }
  }
}
</style>
