<template>
  <div class="account__top" @mousemove="isEdit = true" @mouseout="isEdit = false">
    <AccountFriendModal 
      v-model="isFriendModal" 
      v-if="isFriendModal" 
      :isFriendModal="isFriendModal" 
      :friendArr="$store.state.dataDB.friends"
    />
    <AccountSubModal 
      v-model="isSubModal" 
      v-if="isSubModal" 
      :isSubModal="isSubModal"  
      :arrSub="$store.state.dataDB.subscrise"
    />
    <AccountFoneModal 
      v-if="isUser && isModal"
      :isModal="isModal" 
      @modalFalse="modalFalse"
    />
    <v-img 
      v-if="userName.fone"
      min-width="1120" 
      :src="'/' + userName.fone" 
      class="account__top__image-fone"
    >
      <v-btn 
        @click="isModal = true" 
        class="account__top__image-fone__edit" 
        color="success" 
        @mousemove="isEdit = true" 
        v-if="isEdit && isUser"
      >
        Добавить обои профиля
      </v-btn>
    </v-img>
    <div v-else class="account__top__image-fone">
      <v-btn 
        @click="isModal = true" 
        class="account__top__image-fone__edit" 
        color="success" 
        @mousemove="isEdit = true" 
        v-if="isEdit && isUser"
      >
        Добавить обои профиля
      </v-btn>
    </div>
    <v-card class="account__info d-flex align-center justify-space-around">
      <div class="account__info__left d-flex flex-row">
        <v-img 
          v-if="isImg && userName.img" 
          :src="getImage" 
          class="account__info__logo"
        >
        </v-img>
        <div v-else class="account__info__logo"></div>
        <div class="d-flex flex-column">
          <v-card-title class="account__info__title">
            {{ userName.firstname }} {{ userName.lastname }}
          </v-card-title>
          <div class="d-flex flex-row" v-if="isBlackList">
            <v-btn 
              large 
              color="myPurple" 
              class="account__info__button" 
              style="color: #fff !important; margin-left: 20px; font-size: 18px;"
              @click="$router.push({name: 'Level'})"
            >
              {{userLevel.level}} Уровень
              <p>{{ userLevel.reputations }} / {{ userLevel.target }}</p>
            </v-btn>
            <!-- <p class="account__info__online" v-if="user.data.online">
              Пользователь сейчас онлайн
            </p>
            <p class="account__info__not-online" v-else>Пользователь вышел из платформы</p> -->
            <v-menu bottom center>
              <template v-slot:activator="{ on }">
                <v-btn 
                  v-if="!isUser" 
                  class="account__info__others" 
                  height="45"
                  width="50" 
                  dark 
                  v-on="on"
                >
                  <v-icon size="35">view_headline</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item 
                  class="account__info__others__item"                    
                  @click="userBan"
                  v-if="myAccount.blackList.includes(userName.id) == false"
                >
                  <v-list-item-title>Заблокировать</v-list-item-title>
                </v-list-item>
                <v-list-item 
                  v-if="myAccount.blackList.includes(userName.id)" 
                  class="account__info__others__item"
                  @click="userUnBlocking"
                >
                  <v-list-item-title>Разблокировать</v-list-item-title>
                </v-list-item>
                <v-list-item class="account__info__others__item">
                  <v-list-item-title>Пожаловаться</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </div>
          <div style="margin-left: 30px" v-else>
            <p>Вы были добавлены в черный список</p>
          </div>
        </div>
      </div>
        <div class="account__info__middle text-center">
          <h3 v-if="$store.state.dataDB.friends.length > 0" @click.stop="isFriendModal = true">
            {{ $store.state.dataDB.friends.length }} друзей
          </h3>
          <h3 v-else>Друзей не обнаружено</h3>
          <h3 v-if="$store.state.dataDB.subscrise.length > 0" @click="isSubModal = true">
            {{ $store.state.dataDB.subscrise.length }} подписчиков
          </h3>
          <h3 v-else>Подписчиков не обнаружено</h3>
        </div>
      <div v-if="isBlackList" class="account__info__right d-flex flex-column">
          <v-icon 
            style="cursor: pointer" 
            class="account__info__settings" 
            size="40"
            v-if="isUser"
            @click="$router.push({name: 'Edit'})"
          >
            settings
          </v-icon>
        <v-btn 
          class="account__info__btn" 
          color="myPurple" 
          v-if="!isFriend && !isAddSubs && !isUser 
            && myAccount.friends.includes(userName.id) == false" 
          @click="friendInvite"
        >
          Добавить в друзья
        </v-btn>
        <v-btn 
          class="account__info__btn" 
          color="error" 
          @click="friendDelete"
          v-if="myAccount.friends.includes(userName.id)" 
        >
          Удалить из друзей
        </v-btn>
        <v-btn 
          class="account__info__btn" 
          color="myPurple" 
          v-if="isAddSubs && user.data.subscrise.includes(myAccount.id)" 
          @click="subDelete"
        >
          Отменить запрос на дружбу
        </v-btn>
        <v-btn @click="chatCreate" class="account__info__btn" color="myPurple" v-if="!isUser">
          Написать сообщение
        </v-btn>
      </div>
    </v-card>
  </div>
</template>

<script>
import AccountFoneModal from "./AccountFoneModal.vue";
import AccountFriendModal from "./AccountFriendModal.vue";
import AccountSubModal from "./AccountSubModal.vue";
import { User } from "../../models/models.js"
import axios from "axios";
import "../../scss/accountName.scss";
import "../../scss/mediaAccount.css";

export default {
  name: "AccountName",
  components: { AccountFoneModal, AccountFriendModal, AccountSubModal },
  data() {
    return {
      user: "",
      myAccount: "",
      userName: "",
      userLevel: "",
      isFriendModal: false,
      isModal: false,
      isSubModal: false,
      isFriend: false,
      isAddSubs: false,
      isEdit: false,
      isImg: false,
      img: null
    }
  },
  props: {
    infoName: Object,
    isUser: Boolean,
    isBlackList: Boolean
  },
  async created() {
    this.user = new User(this.$store.state.dataDB)
    this.myAccount = JSON.parse(localStorage.getItem("user"))
    this.userName = this.user.findName
    this.userLevel = this.user.findLevel
    this.img = this.user.findName.img
    this.isImg = true
    let response = await axios.post("/api/users/subs_find", 
      { myId: this.myAccount, userId: this.userName })
    this.isAddSubs = response.data
  },
  computed: {
    getImage() {
      return "/" + this.img
    }
  },
  methods: {
    async friendInvite() {
      await axios.put("/api/users/friend_add/" + this.userName.id + '/' + this.myAccount.id)
      this.isAddSubs = true
      this.isFriend = true
    },
    async subDelete() {
      await axios.delete("/api/users/friend_cancel/" + 
        this.userName.id + '/' + this.myAccount.id)
      this.isAddSubs = false
    },
    async friendDelete() {
      let res = await axios.delete("/api/users/friend_remove/" + 
        this.userName.id + '/' + this.myAccount.id) 
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    async userBan() {
      let res = await axios.put('/api/users/blacklist_add/' + 
        this.userName.id + '/' + this.myAccount.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    async userUnBlocking() {
      let res = await axios.delete("/api/users/blacklist_delete/" + 
        this.userName.id + '/' + this.myAccount.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    modalFalse(data) {
      this.isModal = data
    },
    async chatCreate() {
      let res = await axios.post("/api/chat/chat-create", 
        { userId: this.userName.id, myId: this.myAccount.id })
      this.$router.push({ name: 'Chat', params: { id: res.data.chatId }})
    }
  }
};
</script>