<template>
  <div class="account__wrapper">
    <div class="account d-flex flex-row" v-if="dataDB">
      <Panel />
      <div>
        <AccountName 
          :isBlackList="dataDB.blackList.includes(user.id) == false" 
          :isUser="isUser"
        />
        <AccountSlider 
          v-if="dataDB.blackList.includes(user.id) != true && this.dataDB.images" 
        />
        <div 
          v-if="user.blackList.includes(dataDB.id) == false" 
          class="d-flex flex-row align-start"
        >
          <div class="account__block-left d-flex flex-column">
            <AccountLeft 
              v-if="!isMediaPush && dataDB.blackList.includes(user.id) != true" 
              :dataDB="dataDB" 
            />
            <AccountGroups 
              v-if="!isMediaPush && dataDB.blackList.includes(user.id) != true" 
              :isUser="isUser" 
            />
            <v-date-picker 
              v-if="isUser && !isMediaDate"
              style="margin-top: 50px;" 
              v-model="picker" 
              locale="ru"
              color="myPurple"
            >
            </v-date-picker>
          </div>
          <div v-if="dataDB.blackList.includes(user.id) != true" class="d-flex flex-column">
            <AccountPost v-if="isUser" />
            <div class="account__media__columns">
              <AccountLeft v-if="isMediaPush" :dataDB="dataDB" />
              <AccountGroups v-if="isMediaPush" :isUser="isUser" />
            </div>
            <v-tabs class="account__tabs-wall" color="myPurple">
              <v-tab @click="isWall = false" v-if="isUser" class="account__tabs-tab">
                Мои записи
              </v-tab>
              <v-tab @click="isWall = false" v-else-if="!isUser" class="account__tabs-tab">
                Записи
              </v-tab>
              <v-tab @click="isWall = true" v-if="isUser" class="account__tabs-tab">
                Моя стена
              </v-tab>
              <v-tab @click="isWall = true" v-else-if="!isUser" class="account__tabs-tab">
                Стена
              </v-tab>
            </v-tabs>
            <Posts isRule="account" v-if="!isWall" :dataDB="user" :isUser="isUser" />
            <AccountWall v-if="isWall" :dataDB="user" :isUser="isUser" />
            <Loader v-if="loading" />
          </div>
        </div>
        <div v-else class="text-center" style="margin-top: 30px;">
          <h3>Вы не можете просматривать подробную информацию о пользователе, 
            так как он находится в вашем черном списке</h3>
          <v-btn 
            @click="userUnBlock"
            style="margin-top: 20px;" 
            color="accent" 
            large
          >Разблокировать</v-btn>
        </div>
      </div>
    </div>
    <div 
      class="account__error d-flex flex-row justify-center align-center" 
      v-if="!dataDB && loading == false">
      <div>
        <h2>Аккаунт не найден!</h2>
        <p>Скорее всего такого пользователя не существует, либо он был удален.</p>
        <v-btn 
          class="account__error__btn" 
          color="myPurple"
          @click="$router.push('/')"
        >Вернуться</v-btn>
      </div>
      <div>
        <v-img class="account__error__image" src="../assets/error.jpg"></v-img>
      </div>
    </div>
  </div>
</template>

<script>
import Panel from "../components/Panel.vue";
import AccountName from "../components/accounts/AccountName.vue";
import AccountSlider from "../components/accounts/AccountSlider.vue";
import AccountLeft from "../components/accounts/AccountLeft.vue";
import AccountGroups from "../components/accounts/AccountGroups.vue";
import AccountPost from "../components/accounts/AccountPost.vue";
import AccountWall from "../components/accounts/AccountWall.vue";
import Posts from "../components/Posts.vue";
import Loader from "../components/Loader.vue";
import axios from "axios";
import "../scss/account.scss";
import "../scss/mediaAccount.css";

export default {
  name: "Account",
  components: {
    AccountName,
    AccountSlider,
    AccountLeft,
    AccountGroups,
    AccountPost,
    AccountWall,
    Posts,
    Loader,
    Panel
  },
  metaInfo: {
    title: "MyOcean - Аккаунт"
  },
  data() {
    return {
      loading: true,
      dataDB: "",
      isUser: false,
      isWall: false,
      isMediaDate: false,
      isMediaPush: false,
      user: "",
      picker: new Date().toISOString().substr(0, 10)
    }
  },
  async created() {
    try {
      this.user = JSON.parse(localStorage.getItem("user"))
      let response = await axios.get('/api/users/find_user/' + this.$route.params.id,
      { headers: { Authorization: localStorage.getItem("jwt") }})
      this.loading = false

      this.dataDB = response.data
      if (this.user.id == this.dataDB.id) this.isUser = true
      this.$store.commit("loadData", response.data)

      let userWidth = document.documentElement.clientWidth
      if (userWidth <= 1111) this.isMediaDate = true
      else this.isMediaDate = false
      window.addEventListener("resize", this.mediaView)
      // if (navigator.geolocation) {
      //   navigator.geolocation.getCurrentPosition(pos => {
      //     let lat = pos.coords.latitude
      //     let lon = pos.coords.longitude
      //     axios.put("/api/users/geo-add", { lat: lat, lon: lon, id: this.user.id })
      //   })
      // }
    } catch (error) {
      this.loading = false
      console.log(error.message)
    }
  },
  methods: {
    async userUnBlock() {
      let res = await axios.delete("/api/users/blacklist_delete/" + 
        this.dataDB.id + '/' + this.user.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    mediaView() {
      let userWidth = document.documentElement.clientWidth
      if (userWidth <= 1111) this.isMediaDate = true
      else this.isMediaDate = false

      if (userWidth <= 556) this.isMediaPush = true
      else this.isMediaPush = false
    }
  }
};
</script>