<template>
  <div>
    <div>
      <v-dialog 
        v-model="isSubModal" 
        fullscreen 
        hide-overlay 
        transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="myPurple">
          <v-btn icon dark @click="$emit('input', false)">
            <v-icon size="32">mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title class="dialog__title">Список подписчиков</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <div class="d-flex flex-row flex-wrap">
          <div v-for="(sub, index) in dataDB" :key="index" class="dialog__sub d-flex flex-column align-center">
            <v-img 
              v-if="sub.img" 
              :src="'/' + sub.img"
              class="dialog__sub__image"
            >
            </v-img>
            <div v-else class="dialog__sub__image"></div>
            <router-link
              :to="{name: 'Reload', params: {id: sub.id}}"
              class="dialog__sub__title" 
            >
              {{ sub.firstname }} {{ sub.lastname }}
            </router-link>
          </div>
        </div>
      </v-card>
    </v-dialog>
    </div>
  </div>
</template>

<script>
import { User } from "../../models/models.js";
import axios from "axios";

export default {
  name: "AccountFriendModal",
  data() {
    return {
      dataDB: []
    }
  },
  props: {
    isSubModal: Boolean,
    arrSub: Array
  },
  async created() {
    for (let id of this.arrSub) {
      let response = await axios.get("/api/users/find_user/" + id,
        { headers: { Authorization: localStorage.getItem("jwt") }})
      let user = new User(response.data)
      this.dataDB.push(user.findName)
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog {
  &__sub {
    margin-top: 30px;
    width: 20%;
    &__title {
      color: #7f5af0;
      cursor: pointer;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
    &__image {
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background-color: #2cb67d;
      cursor: pointer;
    }
  }
  &__toolbar {
    margin-left: 100px;
  }
  &__title {
    font-size: 24px;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
  }
}
</style>