<template>
  <div>
    <v-card class="account__post">
      <div class="account__post__top d-flex flex-row">
        <v-img 
          v-if="$store.state.dataDB.additionally.img" 
          :src="getImage" 
          class="account__post__logo"
        >
        </v-img>
        <div v-else class="account__post__logo"></div>
        <quill-editor 
          class="account__post__editor"
          v-model="post.text" 
          ref="myQuillEditor" 
          value="Поделитесь своими мыслями со всем миром!"
          :options="editorOption" 
          @blur="onEditorBlur" 
          @focus="onEditorFocus"
          @change="onEditorChange($event)"
          @ready="onEditorReady($event)"
        ></quill-editor>
      </div>
      <div class="account__post__images" v-if="preImages">
        <div class="d-flex justify-center" v-for="(img, index) of preImages" :key="index">
          <v-img class="account__post__images__logo" max-width="400" :src="img">
            <v-btn
              @click="imageDelete(img, index)" 
              class="account__post__images__close" 
              color="error"
            >
              <v-icon>close</v-icon>
            </v-btn>
          </v-img>
        </div>
      </div>
      <div class="account__post__down d-flex flex-row align-center">
        <v-col cols="1">
          <form>
            <input 
              id="upload"
              class="account__post__file" 
              type="file"
              @input="imageSave" 
            />
            <label class="account__file__label" for="upload">
              <v-icon style="cursor: pointer;">photo</v-icon>
            </label>
          </form>
        </v-col>
        <v-col cols="3">
          <v-select
            v-if="!isCategory"
            v-model="isCategory"
            color="#7f5af0"
            class="account__post__select"
            :items="categories"
            width="200"
            label="Выберите тему"
          ></v-select>
          <v-chip
            class="account__post__select"
            style="cursor: pointer;"
            v-if="isCategory"
            close
            outlined
            label
            color="#7f5af0"
            @click:close="isCategory = ''"
            >{{ isCategory }}</v-chip
          >
        </v-col>
        <v-col cols="4">
          <v-switch
            v-model="isComments"
            color="#7f5af0"
            label="Включить комментарии"
            class="account__post__comments"
          ></v-switch>
        </v-col>
        <v-col cols="4">
          <v-btn 
            v-if="post.text.length < 10" 
            class="account__post__btn" 
            color="myPurple"
            @click="isSymbols = true">
              Рассказать
          </v-btn>
          <v-btn 
            class="account__post__btn"
            v-else
            color="#7f5af0" 
            @click="postCreate">
              Рассказать
          </v-btn>
        </v-col>
      </div>
    </v-card>
    <v-snackbar v-model="isPublic">
      <p>Пост добавлен. Обновите страницу.</p>
      <v-btn color="#7f5af0" style="color: #fff;" @click="isPublic = false">Закрыть</v-btn>
    </v-snackbar>
    <v-snackbar v-model="isSymbols">
      <p>Пост должен содержать больше 10 символов.</p>
      <v-btn color="#7f5af0" style="color: #fff;" @click="isSymbols = false">Закрыть</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import "../../scss/mediaAccount.css";
import "../../scss/accountPost.scss";

export default {
  name: "AccountPost",
  data() {
    return {
      isCategory: "",
      isPublic: false,
      isComments: true,
      isSymbols: false,
      categories: [
        "#IT", "#развлечение", "#спорт", "#IT", "#музыка", "#наука", "#мемы", "#природа",
        "#дизайн", "#политика", "#религия", "#образование", "#новости"
      ],
      dataDB: this.$store.state.dataDB,
      post: {
        text: "",
        isComments: true,
        category: "",
        date: moment().format("LL"),
        authorId: "",
        authorLevel: "",
        id: "",
      },
      images: [],
      preImages: [],
      editorOption: {
        theme: "snow",
        modules: {
          toolbar: [
            [ 'bold', 'italic', 'underline', 'strike' ],
            [{ 'header': 1 }, { 'header': 2 }],
          ],
        }
      }
    };
  },
  computed: {
    getImage() {
      return '/' + this.$store.state.dataDB.additionally.img
    },
  },
  methods: {
    async postCreate() {
      try {
        this.post.isComments = this.isComments;
        this.post.category = this.isCategory;
        this.post.authorId = this.dataDB.id;
        this.post.authorLevel = this.dataDB.level;
        this.post.id = Math.random()

        let formData = new FormData()
        formData.set("isComments", this.post.isComments)
        formData.set("category", this.post.category)
        formData.set("authorId", this.post.authorId)
        formData.set("authorLevel", this.post.authorLevel)
        formData.set("id", this.post.id)
        formData.set("text", this.post.text)
        formData.set("date", this.post.date)
        if (this.images) this.images.map(img => formData.append("img", img, img.name))

        let res = await axios.put("/api/posts/new_post", formData,
        { headers: { 'Content-Type': "multipart/form-data" }});
        console.log(res.data)
        this.post.text = "";
        this.isCategory = "";
        this.isPublic = true;
      } catch (err) {
        console.log(err.message);
      }
    },
    imageSave(event) {
      let img = event.target.files[0]
      this.images.push(img)
      let reader = new FileReader()
      reader.onload = e => this.preImages.push(e.target.result)
      reader.readAsDataURL(event.target.files[0])
    },
    imageDelete(img, index) {
      let isImage = this.preImages.indexOf(img)
      if (isImage != -1) this.preImages.splice(isImage, 1)
      this.images.splice(index, 1)
    },
    checkImage() {},
    onEditorBlur() {},
    onEditorFocus () {},
    onEditorReady() {},
    onEditorChange ({ html }) { this.post.text = html }
  },
};
</script>
